<template>
  <div class="cms-layout">
     <cms-header></cms-header>
     <div class="cms-container">
        <cms-side-bar class="cms-aside"></cms-side-bar>
        <div class="cms-main"  :style="{left:left+'px'}">
            <cms-tag></cms-tag>   
            <keep-alive :include="cacheViews">           
            <router-view class="cms-body" v-if="reload"></router-view> 
             </keep-alive>     
        </div>
     </div>
  </div>
</template>

<script>
import cmsHeader from './header'
import cmsSideBar from './sideBar'
import cmsTag from './tagView'

export default {
  components: {
    cmsHeader,
    cmsSideBar,
    cmsTag
  },
  computed: {
    cacheViews: function() {
      return this.$store.state.tagsView.cachedViews
    },
    left() {
      let value = 220
      if (this.$store.state.sideBar.collapse) {
        if (this.$route.path === '/index') {
          value = 70
        } else {
          value = 220
        }
      } else {
        value = 42
      }
      return value
    }

  },
  data() {
    return {
      collapse: false,
      reload: true
    }
  },
  watch: {
    $route() {
      if (this.$route.path === '/index') {
        this.collapse = false
      }
      this.reload = false
      setTimeout(() => {
        this.reload = true
      }, 0)
    }
  }
}
</script>

<style lang="scss" scoped>
  .ml-42{
  left: 42px;
  }
  .ml-220{
    left: 220px;
  }
</style>






