<template>
  <header class="cms-header" :style="{ backgroundColor:color}">
    <!-- 左侧 -->
    <div class="cms-header-left">
      <div class="logo">
        <img src="../assets/images/logo.png" alt="logo">
      </div>
      <span class="cms-toggle-button" @click="toggleSideBar" title="收缩／展开">
        <i class="iconfont" :class="status?'icon-zhankai-copy':'icon-zhankai'"></i>
      </span>
    </div>
    <!--右侧-->
    <div class="cms-header-right">
      <ul class="cms-nav clearfix">
        <li class="cms-nav-item flex-center toggleInfo">
          <span class="iconfont icon-morentouxiang toggleInfo"></span>
          <span class="toggleInfo">您好,&nbsp;</span>
          <span class="user-name toggleInfo">
            {{userInfo.userName}}
            <i
              class="triangle-bottom toggleInfo"
              :class="infoState?'rotate180':''"
            ></i>
          </span>
          <!-- 展开框 -->
          <transition name="el-zoom-in-top">
            <section class="user-info-wraper" v-show="infoState">
              <div class="user-info-title flex-center">
                <i class="iconfont icon-chakantiezishijian"></i>
                <span class="title-font">最后登录</span>
              </div>
              <div class="user-info-content">
                <p style="color:#444">{{$moment(userInfo.lastLoginTime)}}</p>
                <p style="color:#999">IP: {{userInfo.lastLoginIP}}</p>
              </div>
              <div @click="$routerLink(`/admin/update?type=update&id=${userInfo.userId}`)" class="user-info-footer">
                <i class="iconfont icon-yonghutouxiang"></i>
                <span>修改个人信息</span>
              </div>
            </section>
          </transition>
        </li>
        <li class="cms-nav-item" @click="isShow">
          <span title="产品信息">
            <img src="./../assets/images/zhushi.png" alt class="header-img">
          </span>
          <cms-aside :show="show"></cms-aside>
        </li>
        <li class="cms-nav-item" @click="setIndexInfo">
          <span title="重置索引">
            <img src="./../assets/images/header_primary.png" alt class="header-img">
          </span>
        </li>
        <li class="cms-nav-item">
          <el-popover
            placement="bottom"
            width="260"
            transition="fade-in-linear"
            v-model="showInfo"
            @show="showPopover"
            trigger="click"
          >
            <ul class="info-wrap t-14">
              <li>
                <span>站内消息通知</span>
              </li>
              <li
                class="t-12 t-bottom info-box"
                v-for="(item,index) in unreadInfo.infoData"
                :key="index"
              >
                <div class="text-overflow">
                  <a @click="toInfo('/station/view','view',{id:item.messageId})">{{item.content}}</a>
                </div>
                <div>{{$moment(item.createTime)}}</div>
              </li>
              <li v-if="unreadInfo.infoData&&unreadInfo.infoData.length <=0" class="t-12">
                <p>暂无未读信息</p>
              </li>
              <li v-if="unreadInfo.infoData&&unreadInfo.infoData.length >0" class="t-14 t-center">
                <a @click="toInfo('/station/list','list')">查看更多</a>
              </li>
            </ul>
            <span slot="reference" title="未读消息" class="t-block">
              <img src="./../assets/images/header_message.png" alt class="header-img">
            </span>
          </el-popover>
          <el-badge
            class="cms-header-mark"
            :value="unreadInfo.infoNum"
            :max="99"
            :hidden="unreadInfo.infoNum<=0"
          />
        </li>
        <!-- <li class="cms-nav-item">
            <span title="更换主题色">
              <img src="./../assets/images/header_theme.png" alt="" class="header-img">
            </span>
              <theme-picker title="更换主题色"></theme-picker> 
        </li>-->
        <li class="cms-nav-item" @click="$routerLink('/index')">
          <span title="控制台">
            <img src="./../assets/images/header_index.png" alt class="header-img">
          </span>
        </li>
        <li class="cms-nav-item" @click="logOut">
          <span title="退出">
            <img src="./../assets/images/header_logout.png" alt class="header-img">
          </span>
        </li>
      </ul>
    </div>
  </header>
</template>

<script>
import themePicker from './ThemePicker'
import cmsAside from './cms/cmsAside'
import { setIndex } from '@/api/global.js'
export default {
  components: {
    themePicker,
    cmsAside
  },
  data() {
    return {
      show: false,
      showInfo: false,
      infoState: false // 登陆信息显示状态
    }
  },
  computed: {
    status() {
      return this.$store.state.sideBar.collapse
    },
    userInfo() {
      return this.$store.state.user
    },
    color: {
      get() {
        return this.$store.state.system.color
      },
      set() {}
    },
    unreadInfo() {
      return this.$store.state.unreadInfo
    }
  },
  methods: {
    setIndexInfo() {
      this.$confirm(`
        <div style="font-size:14px;"> 
          商品索引是商城搜索的唯一依据，如出现以下情况，你可重置索引将商城内所有已上架的商品生成索引：<br/>
            • 新上架的商品检索不到<br/>
            • 修改上架的商品检索不到或依然能检索到<br/>
            • 上架/下架的商品检索不到或依然能检索到<br/> 
            • 店铺营业状态变更后，店铺所经营的商品检索不到或依然能检索到<br/>
           </div> 
          `, '提示', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '确认重置',
        cancelButtonText: '取消',
        type: 'warning',
        customClass: 'bigger-message'
      }).then(res => {
        setIndex().then(res => {
          if (res.code === 200) {
            this.$successMessage('重置成功')
          }
        })
      }).catch(res => {
        return false
      })
    },
    showPopover() {
      this.showInfo = true
    },
    toInfo(to, type, params) {
      this.showInfo = false
      this.$routerLink(to, type, params)
    },
    isShow() {
      // 是否显示侧边栏
      this.$store.dispatch('toggleAside', true)
    },
    toggleSideBar() {
      this.$store.dispatch('toggleSideBar', !this.status)
      this.$store.dispatch('toggleAuto', this.status)
    },
    autoToggle() {
      if (document.body.clientWidth < 1300) {
        this.$store.dispatch('toggleSideBar', false)
      } else {
        this.$store.dispatch('toggleSideBar', true)
      }
    },
    logOut() {
      this.$confirm('是否确定退出?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$store
            .dispatch('logOut', { token: localStorage.getItem('platKey') })
            .then(res => {
              localStorage.setItem('platKey', '')
              location.reload()
            })
        })
        .catch(() => {})
    }
  },
  mounted() {
    const that = this
    this.autoToggle()
    window.onresize = () => {
      return (() => {
        that.autoToggle()
      })()
    }
    document.addEventListener('click', e => {
      if (e.target.className.indexOf('toggleInfo') >= 0) {
        this.infoState = !this.infoState
      } else {
        this.infoState = false
      }
    })
  },
  created() {
    this.$store.dispatch('setInfoData')
  }
}
</script>
<style scoped>
.info-wrap {
  padding: 5px;
}
.info-box {
  padding: 3px;
  line-height: 20px;
}
.t-center {
  text-align: center;
  line-height: 20px;
  padding-top: 16px;
}
.t-14 {
  font-size: 14px;
}
.t-12 {
  font-size: 12px;
}
.t-bottom {
  border-bottom: 1px solid #f7f7f7;
}
.t-block {
  display: inline-block;
  height: 100%;
  width: 100%;
}
</style>






