<template>
      <div>
        <div class="aside_wrap" :class="showThe?'aside_wrap--active':''">
        <div class="aside" v-if="infoData" @click.stop="showData" v-loading="loading">
          <span class="aside-title">信息</span>
          <div class="aside-cont t-bottom">
            <span class="aside-info">产品信息</span>
            <ul class="aside-cont">
              <li>产品标识：{{infoData.product.identification}}</li>
              <li>产品名称：{{infoData.product.name}}</li>
              <li>产品版本：{{infoData.product.version}}</li>
              <li class="t-12">产品许可证：期限授权（{{infoData.product.authorization}}到期）
                <br>
              <el-button size="mini" type="primary" @click.stop="dialogVisible=true">更换授权</el-button></li>
              <li>最近启动时间：{{infoData.product.startTime}}</li>
            </ul>
          </div>
          <div class="aside-cont t-bottom">
            <span class="aside-info">运行环境</span>
            <ul class="aside-cont">
              <li>操作系统：{{infoData.environment.system}}</li>
              <li>JDK版本：{{infoData.environment.jdk}}</li>
            </ul>
          </div>
          <div class="aside-cont">
            <span class="aside-info">数据库信息</span>
            <ul class="aside-cont">
              <li>数据库类型：{{infoData.database.databaseType}}</li>
              <li>数据库链接地址：{{infoData.database.url}}</li>
              <li>数据库链接端口：{{infoData.database.port}}</li>
              <li>数据库名称：{{infoData.database.databaseName}}</li>
            </ul>
          </div>
        </div>
      </div>
      <el-dialog
          title="产品授权"
          :visible.sync="dialogVisible"
          width="40%"
          :close-on-click-modal="false"
          :before-close="handleClose">
          <div class="t-12 t-font">
            <p style="margin-bottom:5px"><i class="el-icon-warning" style="color:#FFA000" ></i> 温馨提示： </p>
            <p class="t-padding">• 授权码为验证系统许可证的唯一标识，妥善保管，请勿泄漏给他人</p>
            <p class="t-padding">• 单个授权码只能授权一个顶级域名，请勿更换访问域名，如需变更请前往官网联系客服</p>
            <p class="t-padding">• 确保产品能正常授权成功，请保证系统网络连接正常</p>
            <p class="t-padding">• 授权码请前往JEECMS官网进行购买   <a style="color:#66B1FF" href="http://www.jeecms.com/" target="_blank">去购买</a></p>
            <el-input maxlength="50" :autofocus="true" ref="first" v-model="attrValue" placeholder="请输入授权码" type="textarea" ></el-input>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click.stop="handleClose">取 消</el-button>
            <el-button size="mini" :disabled="attrValue===''" type="primary" @click.stop="getRole">确 定</el-button>
          </span>
        </el-dialog>
      </div>
</template>

<script>
import { productinfo, commandChange } from './../../api/global.js'
export default {
  name: 'cmsAside',
  props: {
    show: {// 是否显示侧边栏
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      infoData: null,
      dialogVisible: false,
      attrValue: ''
    }
  },
  computed: {
    showThe() {
      return this.$store.state.sideBar.asideStatus
    }
  },
  methods: {
    handleClose() { // 关闭弹窗
      this.dialogVisible = false
      this.$store.dispatch('toggleAside', false)
    },
    getRole() { // 获取权限
      var params = {
        attrValue: this.attrValue
      }
      commandChange(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('成功授权！')
          this.handleClose()
        } else {
          this.handleClose()
        }
      })
    },
    hideAside() { // 隐藏侧边栏
      if (!this.dialogVisible) {
        this.$store.dispatch('toggleAside', false)
      }
    },
    showData() { // 显示侧边栏
      this.$store.dispatch('toggleAside', true)
    },
    showInfo() { // 显示侧边栏信息
      this.loading = true
      productinfo().then(res => {
        if (res.code === 200) {
          this.infoData = res.data
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    }
  },
  watch: {
    showThe(value) {
      if (!this.infoData) {
        this.showInfo()
      }
      if (value) {
        window.addEventListener('click', this.hideAside)
      } else {
        window.removeEventListener('click', this.hideAside)
      }
    }
  },

  created() {
  }
}
</script>

<style lang="scss" scoped>
 
  .aside_wrap{
    position: fixed;
    top: 54px;
    right: 0;
    width: 330px;
    height: 100%;
    background-color: #E9E9E9;
    z-index: 4002;
      transition: 0.25s transform;
   transform: translateX(100%);
  }
  .aside_wrap--active{
 transform: translateX(0);
  }
  .aside{
    padding: 0 20px;
    box-sizing: border-box;
    color: #666;
    width: 100%;
    height: 100%;
    text-align: left;
    line-height: 24px;
    transition: 1s;
  }
  .aside-title{
    font-size: 16px;
    line-height: 48px;
  }
  .aside-cont{
    font-size: 14px;
    padding: 5px 10px 5px;
  }
  .aside-info{
    font-size: 14px;
  }
  .t-bottom{
    border-bottom: 1px solid #ccc;
  }
  .t-12{
    font-size: 12px;
  }
  .t-font{
    color: #999;
    line-height: 16px;
    text-align: left;
    padding: 20px;
  }
  .t-padding{
    padding-left: 10px;
    padding-bottom: 10px;
  }
</style>




